<template>
  <div style="width:97%;padding:20px">
    <el-card shadow="never" class="box-card">
      <div>
        <i style="float:left" class="el-icon-search"></i>
        <span style="float:left;line-height:1"></span>
        <button
          type="button"
          class="el-button el-button--primary el-button--small"
          style="float: right;"
          @click="search"
        >
          <span></span>
        </button>
        <button
          type="button"
          class="el-button el-button--default el-button--small"
          style="float: right; margin-right: 15px;"
        >
          <span></span>
        </button>
      </div>
      <el-form
        :inline="true"
        :model="formInline"
        class="demo-form-inline"
        style="margin-top:15px;clear:both"
      >
        <el-form-item style="line-height:1" label="" label-width="140px">
          <el-input v-model="formInline.search" placeholder="" size="small"></el-input>
        </el-form-item>
        <el-form-item style="line-height:1" label="" label-width="140px">
          <el-input v-model="formInline.number" placeholder="" size="small"></el-input>
        </el-form-item>
        <el-form-item style="line-height:1" label="" label-width="140px">
          <el-cascader v-model="formInline.sort" :options="options1" clearable size="small"></el-cascader>
        </el-form-item>
        <el-form-item style="line-height:1" label="" label-width="140px">
          <el-select v-model="formInline.brand" placeholder="" size="small">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="line-height:1" label="" label-width="140px">
          <el-select v-model="formInline.state" placeholder="" size="small">
            <el-option
              v-for="item in options3"
              :key="item.value"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="line-height:1" label="" label-width="140px">
          <el-select v-model="formInline.status" placeholder="" size="small">
            <el-option
              v-for="item in options4"
              :key="item.value"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card class="box-card" shadow="never" style="margin-top:20px">
      <div class="clearfix">
        <i style="float:left" class="el-icon-tickets"></i>
        <span style="float:left;line-height:1"></span>
        <el-button plain style="float:right" size="mini">
          <el-link :underline="false" href="#/addshop"></el-link>
        </el-button>
      </div>
    </el-card>
    <el-table
      border
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top:20px"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="57" align="center"></el-table-column>
      <el-table-column type="index" label="" width="100" align="center"></el-table-column>
      <el-table-column label="" width="110" align="center">
        <template slot-scope="scope">
          <el-image style="width:80px" :src="scope.row.url"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="" width="83" align="center">
        <template slot-scope="scope">
          <p>{{scope.row.mingchen1}}</p>
          <p>{{scope.row.mingchen2}}</p>
        </template>
      </el-table-column>
      <el-table-column label="/" width="110" align="center">
        <template slot-scope="scope">
          <p>{{scope.row.price}}</p>
          <p>{{scope.row.huohao}}</p>
        </template>
      </el-table-column>
      <el-table-column label="" width="130" align="center">
        <template slot-scope="scope">
          <p>
            <span></span>
            <el-switch v-model="scope.row.putaway"></el-switch>
          </p>
          <p>
            <span></span>
            <el-switch v-model="scope.row.new_prodce"></el-switch>
          </p>
          <p>
            <span></span>
            <el-switch v-model="scope.row.recommend"></el-switch>
          </p>
        </template>
      </el-table-column>
      <el-table-column prop="sort" label="" width="90" align="center"></el-table-column>
      <el-table-column label="SKU" width="95" align="center">
        <template>
          <el-button type="primary" icon="el-icon-edit" circle></el-button>
        </template>
      </el-table-column>
      <el-table-column prop="sales" label="" width="90" align="center"></el-table-column>
      <el-table-column label="" width="95" align="center">
        <template slot-scope="scope">
          <p>{{scope.row.check}}</p>
          <el-button type="text"></el-button>
        </template>
      </el-table-column>
      <el-table-column label="" width="150" align="center">
        <template slot-scope="scope">
          <p>
            <el-button size="small"></el-button>
            <el-button size="small"></el-button>
          </p>
          <p>
            <el-button size="small"></el-button>
            <el-button size="small" type="danger" @click="del(scope.row._id,scope.$index)"></el-button>
          </p>
        </template>
      </el-table-column>
    </el-table>
    <el-select  v-model="value" placeholder="" style="margin-top:20px;float:left" size="small">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <el-button @click="change" type="primary" size="small" style="margin: 20px 160px 0 0px;"></el-button>
    <el-pagination
      style="float:right;margin-top:20px"
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="pages"
      :page-size="5"
      layout="total, sizes, prev, pager, next, jumper"
      :total="alldata.length"
    ></el-pagination>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      pagesnum: 5,
      pages: [5, 10, 15],
      alldata: [],
      currentPage: 1,
      formInline: {
        search: "",
        number: "",
        sort: "",
        brand: "",
        state: "",
        status: ""
      },
      options: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
