        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        },
        {
          value: "7",
          label: ""
        }
      ],
      options1: [
        {
          label: "",
          value: "cloth",
          children: [
            {
              value: "waitao",
              label: ""
            },
            {
              value: "Txu",
              label: "T"
            },
            {
              value: "xiuxianku",
              label: ""
            },
            {
              value: "niuzaiku",
              label: ""
            },
            {
              value: "chenshan",
              label: ""
            },
            {
              value: "nanxie",
              label: ""
            }
          ]
        },
        {
          value: "shoujishuma",
          label: "",
          children: [
            {
              value: "tongxun",
              label: ""
            },
            {
              value: "peijian",
              label: ""
            },
            {
              value: "sheying",
              label: ""
            },
            {
              value: "yule",
              label: ""
            },
            {
              value: "shuma",
              label: ""
            },
            {
              value: "shebei",
              label: ""
            }
          ]
        },
        {
          value: "dianqi",
          label: "",
          children: [
            {
              value: "dianshi",
              label: ""
            },
            {
              value: "kongtiao",
              label: ""
            },
            {
              value: "xiyiji",
              label: ""
            },
            {
              value: "bingxiang",
              label: ""
            },
            {
              value: "dadian",
              label: ""
            },
            {
              value: "xiaodian",
              label: ""
            },
            {
              value: "shenghuo",
              label: ""
            },
            {
              value: "jiankang",
              label: ""
            }
          ]
        },
        {
          value: "jiazhuang",
          label: "",
          children: [
            {
              value: "yuwei",
              label: ""
            },
            {
              value: "dengshi",
              label: ""
            },
            {
              value: "wujin",
              label: ""
            },
            {
              value: "woshi",
              label: ""
            },
            {
              value: "keting",
              label: ""
            }
          ]
        },
        {
          value: "qiche",
          label: "",
          children: [
            {
              value: "zhengche",
              label: ""
            },
            {
              value: "chezai",
              label: ""
            },
            {
              value: "wujin",
              label: ""
            },
            {
              value: "zhuangshi",
              label: ""
            }
          ]
        }
      ],
      options2: [
        {
          label: "",
          value: "xiaomi"
        },
        {
          label: "",
          value: "qipilang"
        },
        {
          label: "",
          value: "hailan"
        },
        {
          label: "",
          value: "pingguo"
        },
        {
          label: "",
          value: "sanxing"
        },
        {
          label: "",
          value: "huawei"
        },
        {
          label: "",
          value: "geli"
        },
        {
          label: "",
          value: "fantai"
        },
        {
          label: "",
          value: "wanhe"
        },
        {
          label: "OPPO",
          value: "oppo"
        },
        {
          label: "NIKE",
          value: "nike"
        }
      ],
      options3: [
        {
          label: "",
          value: "shangjia"
        },
        {
          label: "",
          value: "xiajia"
        }
      ],
      options4: [
        {
          label: "",
          value: "tongguo"
        },
        {
          label: "",
          value: "weishenhe"
        }
      ],
      tableData: []
    };
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      this.pagesnum = val;
      this.tableData = this.alldata.slice(
        (this.currentPage - 1) * this.pagesnum,
        this.pagesnum * this.currentPage
      );
    },
    handleCurrentChange(val) {
      this.tableData = this.alldata.slice(
        (val - 1) * this.pagesnum,
        this.pagesnum * val
      );
    },
    async del(val, index) {
      var _this = this;
      await this.$axios
        .get("http://localhost:3000/delshop?_id=" + val)
        .then(async function(data) {
          if (data.status == 200) {
            _this.alldata.splice(index - 1, 1);
            _this.tableData = _this.alldata.slice(
              (_this.currentPage - 1) * _this.pagesnum,
              _this.pagesnum * _this.currentPage
            );
          }
        });
    },
    async search() {
      let data = await this.$axios.get(
        "http://localhost:3000/selectshop?mingchen1=" +
          this.formInline.search +
          "&mingchen2=" +
          this.formInline.brand +
          "&putaway=" +
          this.formInline.state
      );
      this.formInline.search = this.formInline.brand = this.formInline.state =
        "";
      this.alldata = data.data;
      this.tableData = this.alldata.slice(
        (this.currentPage - 1) * this.pagesnum,
        this.pagesnum * this.currentPage
      );
    },
    change() {
      let val = this.value;
      console.log(val);
      if (val == "0") {
        this.tableData.forEach(item => {
          item.putaway = true;
        });
      }
      if (val == "1") {
        this.tableData.forEach(item => {
          item.putaway = false;
        });
      }
      if (val == "4") {
        this.tableData.forEach(item => {
          item.new_prodce = true;
        });
      }
      if (val == "5") {
        this.tableData.forEach(item => {
          item.new_prodce = false;
        });
      }
      if (val == "2") {
        this.tableData.forEach(item => {
          item.recommend = true;
        });
      }
      if (val == "3") {
        this.tableData.forEach(item => {
          item.recommend = false;
        });
      }
    }
  },
  async created() {
    let data = await this.$axios.get("http://localhost:3000/findlist");
    this.alldata = data.data;
    this.tableData = this.alldata.slice(
      (this.currentPage - 1) * this.pagesnum,
      this.pagesnum * this.currentPage
    );
  }
};
</script>
