import Vue from 'vue'
import Router from 'vue-router'
// import Home from './views/Home.vue'
import Login from './components/Login.vue'
import Listview from './views/listview.vue'
import Addshopview from './views/addshopview.vue'
import Home from './views/home'
import Souye from './views/Tabbar/souye'

//
import New from './views/Tabbar/new'
Vue.use(Router)

export default new Router({
  routes: [{
      name: "home",
      path: "/home",
      component: Home,
      children: [{
          name: 'Souye',
          // path/
          path: 'souye',
          component: Souye,
        },
        {
          name: 'New',
          // path/
          path: 'new',
          component: New,
        },{
          name:'listshop',
          path: 'listshop',
          component: Listview,
        }, {
          name:'addshop',
          path: 'addshop',
          component: Addshopview,
        }, {
          //router  orderlist
          path: 'OL',
          name: 'OL',
          component: () => import( /* webpackChunkName: "about" */ './views/orderList.vue') //
        }, {
          //router    ordersForShipment
          path: 'OFS/:id',
          name: 'OFS',
          component: () => import( /* webpackChunkName: "about" */ './views/ordersForShipment.vue') //
        }, {
          //router orderDetails
          path: 'OD/:id',
          name: 'OD',
          component: () => import( /* webpackChunkName: "about" */ './views/orderDetails.vue') //
        }, {
          //router returnApplication
          path: 'RA',
          name: 'RA',
          component: () => import( /* webpackChunkName: "about" */ './views/returnApplication.vue') //
        }, {
          //router returnApplicationDetails
          path: 'RAD',
          name: 'RAD',
          component: () => import( /* webpackChunkName: "about" */ './views/returnApplicationDetails.vue') //
        }
      ]
    },
    {
      name:'login',
      path: '/login',
      component: Login
    },  {
      path: '/',
      redirect: '/login'
    }, {
      //sign in
      path: '/signin',
      name: 'signin',
      component: () => import( /* webpackChunkName: "about" */ './views/signIn.vue') //
    },
  ]
})
